import React, { useState, useEffect } from 'react';
import Header from '../components/Common/Header/Header';
import NavBar from '../components/Common/NavBar';
import styled from 'styled-components';
import GameList from '../components/List/GameList';
import SelectFilter from '../components/Common/Filter/SelectFilter';
import {
  getGameInfo,
  getTeamName,
  filterGameInfo,
} from '../api/GameAPI/AddGameAPI';
import { useRecoilState } from 'recoil';
import { userToken } from '../atom/loginAtom';
import Empty from '../components/Common/Empty';
import GameLoader from '../components/Skeleton/GameLoader';

const MainStyle = styled.main`
  padding: 50px 0 60px;
  .section-header {
    border-bottom: 0.5px solid var(--color-maingrey);
    font-size: 16px;
    font-weight: bold;
    padding: 16px;
    display: flex;
    align-items: center;
    position: fixed;
    width: 390px;
    background: white;
    gap: 10px;
    div:nth-child(2) {
      margin-left: auto;
    }
  }
  .section-game {
    padding: 80px 16px 16px;
  }
`;

export default function AddGame() {
  const [isLoad, setIsLoad] = useState(false);
  const [token, setToken] = useRecoilState(userToken);
  const [game, setGame] = useState([]); // total game    
  const [filterGame, setFilterGame] = useState([]); // filtered game   
  const [team, setTeam] = useState([]); //    
  const [sport, setSport] = useState(['', '', '', '']); //    
  const [selectTeam, setSelectTeam] = useState(''); //     
  const [selectSport, setSelectSport] = useState(''); //     

  //       
  useEffect(() => {
    const getData = async () => {
      setIsLoad(true);
      const gameData = await getGameInfo(token);
      setGame(gameData);
      setFilterGame(gameData);
      setIsLoad(false);
    };
    getData();
    setTeam(getTeamName());
  }, []);

  //    /       
  useEffect(() => {
    const filteredGame = filterGameInfo(game, '', selectTeam);
    setFilterGame(filteredGame);
  }, [selectTeam]);

  return (
    <>
      <h1 className='a11y-hidden'> </h1>
      <Header text=' ' />
      <MainStyle>
        <section className='section-header'>
          <h2> </h2>
          <SelectFilter
            type=''
            items={sport}
            selectItem={selectSport}
            setSelectItem={setSelectSport}
          />
          <SelectFilter
            type=''
            items={team}
            selectItem={selectTeam}
            setSelectItem={setSelectTeam}
          />
        </section>
        <section className='section-game'>
          {!isLoad && filterGame.length === 0 && (
            <Empty
              message='    !'
              btnText=''
              link='/search/SPORT_'
            />
          )}
          {isLoad ? <GameLoader /> : <GameList games={filterGame} />}
        </section>
      </MainStyle>
      <NavBar />
    </>
  );
}
